import React, { Component } from 'react';
import {Icon ,Tabs,Grid,List} from 'antd-mobile';
import styles  from './detail.less';
import { request } from '../../../../request';
import api from '../../../../request/api';
export default class myAfterSale extends Component {
    state = {
      hisNum:'A801233946',
      Handle:'已处理'
      
    }
    componentDidMount(){
        
    }
    getPaletteClassify = () => {
        request({ url: api.boardTypeList, data: { pageNo: 1, pageSize: 10, status: 1 } }).then(res => {
            const { list } = res;
            this.setState({ classifyList: list })
        }).catch(err => { console.log(err) })
    }
    back=()=>{
        this.props.history.goBack()
    }
    toAdd=()=>{
        const { history, match } = this.props;
        history.push({
            pathname:'/myAfterSale/apply',
            state: {}
        })
    }
    render() {
        let {hisNum,Handle} = this.state;
        const photoData  = Array.from(new Array(4)).map((_val, i) => ({
            icon: 'https://gw.alipayobjects.com/zos/rmsportal/nywPmnTAvTmLusPxHPSu.png',
          }));
        return (
            <div className={styles.myAfterSaleDetail}>   
                {/* <div className ={styles.nothing}>暂无售后记录</div> */}
                <div className={styles.historyList}>
                    <List.Item className={styles.mb_8+' '+styles.historyTop}> 
                    <div ><span className={styles.Handle}>{Handle}</span>
                    当前售后单 : {hisNum} 
                    <Icon size ="lg" className={styles.fr +" "+styles.icon} 
                    type="down" theme="outlined" 
                    onClick={this.back} />
                    </div></List.Item>
                   
                    <Tabs
                        tabs={[
                            { title: '诉求详情' },
                            { title: '处理结果' },
                        ]}
                        tabBarTextStyle={{ color: '#6b6b6b' }}
                        tabBarActiveTextColor='#3399ff'
                        tabBarUnderlineStyle={{ borderColor: '#3399ff',width:'30%',margin:'0 10%'}}
                    >
                        <div className={styles.tabBody}>
                            <div className={styles.item}> 
                            <List.Item >
                            销货单号
                            </List.Item>
                            </div>
                            <div className={styles.item}>
                            <List.Item >
                            诉求
                            </List.Item>
                            </div>
                            <div className={styles.item}>
                            <List.Item >
                            受理日期
                            </List.Item></div>
                            <div className={styles.item}><List.Item >要求完成<br/>日期</List.Item></div>
                            <div className={styles.item}><List.Item >问题描述</List.Item></div>
                            <div className={styles.item}><List.Item >照片<br/>
                            <Grid data={photoData}   hasLine={false}/>
                            </List.Item>
                            </div>

                        </div>
                        <div className={styles.tabBody}>
                            <div className={styles.item}><List.Item>处理方式</List.Item></div>
                            <div className={styles.item}><List.Item>处理说明</List.Item></div>
                            <div className={styles.item}><List.Item>客户满意度</List.Item></div>
                            <div className={styles.item}><List.Item>现场照片</List.Item></div>
                        </div>
                    </Tabs>
                </div>
                <div className={styles.plusBtn}><Icon type="plus" onClick={this.toAdd}/></div>
            </div>
        );
    }
}